import React from "react";
import Card from "../Card/Card";
import importAllImagesText from '../Helpers/ImportAllImagesText';

const hotelsImages = await importAllImagesText(require.context('../PartnersLogos/Hotels', false, /\.(webp)$/));

const Hotels = () => {
    return (
        <>
            <h2>Hotels</h2>
            {!hotelsImages.length && <h4 style={{textAlign:'center'}}>No Partners Yet!</h4>}
            <div className="section-container">
                {hotelsImages.length > 0 &&  hotelsImages.map((hotelImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index}
                        image={hotelImage.image}
                        partner_name={hotelImage.name}
                    />
                ))}
            </div>
        </>
    );
}

export default Hotels;